* {
  box-sizing: border-box;
}

body, p {
  margin: 0;
}

.form-container {
  background-color: #353598;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.search-form {
  margin-left: auto;
  margin-right: auto;
}

.input {
  margin-right: 10px;
  padding: 10px 20px;
}

.button, .load-more {
  padding: 10px 20px;
}

.gallery {
  flex-wrap: wrap;
  gap: 10px;
  padding: 20px;
  display: flex;
}

.photo-card {
  border-radius: 4px;
  flex-basis: calc(25% - 7.5px);
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.card-image {
  width: 100%;
  height: 220px;
  object-fit: cover;
  display: block;
}

.info {
  gap: 10px;
  padding: 10px;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:last-child {
  flex-grow: 2;
}

.load-more {
  margin: 0 auto;
  display: block;
}

.is-hidden {
  visibility: none;
  opacity: 0;
  pointer-events: none;
}

